html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.container {
  width: 960px;
  margin: 0 auto;
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 970px;
  }
}

@media only screen and (max-width: 993px) {
  .container {
    width: 750px;
  }
}

@media only screen and (max-width: 769px) {
  .container {
    width: 100%;
    padding: 0px 15px;
  }
}

.header {
  margin: 20px 0px;
  width: 100%;
  position: absolute;
}

.logo {
  display: inline-block;
}

.nav {
  float: right;
  margin-top: 15px;
}

.nav:after {
  content: "";
  display: table;
  clear: both;
}

@media only screen and (max-width: 769px) {
  .nav {
    display: none;
    float: none;
    text-align: center;
  }
}

.nav__item {
  display: inline-block;
  margin-left: 28px;
}

@media only screen and (max-width: 769px) {
  .nav__item {
    display: block;
    margin-top: 15px;
  }
}

.nav__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #b8b8b8;
  font-size: 16px;
  font-family: 'bebas';
}

.nav__link:hover {
  color: #e75f3b;
}

.responsive-menu {
  width: 35px;
  height: 35px;
  position: relative;
  cursor: pointer;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

@media only screen and (max-width: 769px) {
  .responsive-menu {
    display: inline-block;
    position: absolute;
    top: 4px;
    right: 35px;
  }
}

.responsive-menu span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: #e75f3b;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.responsive-menu span:nth-child(1) {
  top: 0px;
}

.responsive-menu span:nth-child(2) {
  top: 13px;
}

.responsive-menu span:nth-child(3) {
  top: 13px;
}

.responsive-menu span:nth-child(4) {
  top: 26px;
}

.responsive-menu.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.responsive-menu.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.responsive-menu.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.responsive-menu.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

@media only screen and (max-width: 769px) {
  .responsive {
    display: block;
  }
}
